
.navbar {
    width: 100%;
    background-color: #fff;
    border-bottom: 1px solid #e0e0e0;
  }
  
  .navbar-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
  }
  
  .navbar-logo {
    font-size: 24px;
    font-weight: bold;
  }
  
  .navbar-search input {
    width: 400px;
    padding: 8px;
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  
  .navbar-buttons button {
    margin-left: 10px;
    padding: 8px 16px;
    border: none;
    border-radius: 4px;
    background-color: #000;
    color: #fff;
    cursor: pointer;
  }
  
  .navbar-bottom {
    display: flex;
    justify-content: space-around;
    padding: 10px 20px;
    background-color: #f8f8f8;
  }
  
  .navbar-bottom .dropdown {
    position: relative;
  }
  
  .navbar-bottom .dropdown:hover .dropdown-content {
    display: block;
  }
  
  .navbar-bottom .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f8f8f8;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  
  .navbar-bottom .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
  
  .navbar-bottom .dropdown-content a:hover {
    background-color: #ddd;
  }
  
  .navbar-bottom a {
    color: black;
    text-decoration: none;
    padding: 10px 20px;
    display: inline-block;
  }
  
  .navbar-bottom a:hover {
    background-color: #ddd;
  }
  
  @media (max-width: 768px) {
    .navbar-top {
      flex-direction: column;
    }
    
    .navbar-search input {
      width: 100%;
    }
    
    .navbar-bottom {
      flex-direction: column;
    }
  
    .navbar-bottom a,
    .navbar-bottom .dropdown {
      width: 100%;
      text-align: center;
    }
  }
  